<template>
  <div class="content">
    <div class="aside">详情</div>
    <div class="content-right">
      <div class="title">
        当前位置*详情
      </div>

      <div class="inside">
        <div class="wrap_article">
          <h1 class="title_article">{{obj.title}}
            <span class="time">{{obj.createdTime|dateformat}}</span>

          </h1>
          <div class="detail"
               v-html="obj.editorContent"></div>
        </div>
      </div>
    </div>
  </div>

</template>
<script >
export default {
  data () {
    return {
      searchForm: {
        pageNum: 1,
        pageSize: 10,
        // total: 100
      },
      obj: {},
      list: [
        {
          title: '测试标题',
          createdTime: '1568214274739'
        }
      ],
      activeName: ''
    }
  },
  methods: {

  },
  created () {
    this.obj = this.$route.query.obj
  },

}
</script>
<style lang="scss" scoped>
.wrap_article {
  padding: 20px;
}
.wrap_article {
  padding: 20px;
  .title_article {
    font-size: 26px;
    line-height: 87px;
    color: #333333;
    border-bottom: 1px solid #cccccc;
    text-align: center;
    position: relative;
    .time {
      font-size: 14px;
      color: #999999;
      right: 20px;
      bottom: 40px;
      height: 20px;
      position: absolute;
    }
  }
  .detail {
    padding: 20px 0;
  }
}
</style>




